.portrait {
    position: fixed;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 45%;
    z-index: 10; }
  
  .active-pot {
    opacity: 1;
    transition: all 0.5s ease-in; }
    @media (max-width: 767px) {
      .active-pot {
        opacity: 0; } }
  
  .inactive-pot {
    opacity: 0;
    transition: all 0.5s ease-in; }
  
  @media (max-width: 767px) {
    .mobile-bg {
      opacity: 1; } }
  
  @media (max-width: 767px) {
    .portrait {
      width: 100%;
      z-index: 1; } }
  
  .woman {
    height: inherit;
    object-fit: cover;
    object-position: center left;
    width: 100%; }
  
  @keyframes fade {
    0% {
      opacity: 1; }
    20% {
      opacity: 1; }
    34% {
      opacity: 0; }
    88% {
      opacity: 0; }
    100% {
      opacity: 1; } }
  
  @keyframes fade-in {
    0% {
      opacity: 0; }
    100% {
      opacity: 1; } }
  
  .rotating-ring {
    position: fixed;
    right: -72%;
    bottom: -170%;
    transform-origin: center;
    width: 147%; }
    @media (max-width: 540px) {
      .rotating-ring {
        display: none; } }
    .rotating-ring img {
      animation: rotate 30s linear 0s infinite normal;
      width: 100%; }
  
  @keyframes rotate {
    from {
      transform: rotate(0deg); }
    to {
      transform: rotate(360deg); } }